import React, { useEffect, useState } from 'react'
import "./Button.css"

const Button = ({captureData}) => {
    const [data, setData] = useState({});

    useEffect(() => {   
        setData(captureData)
        console.log(captureData,"from button");
    },[])

  const handleButtonClick = () => {
    // Construct the URL with the data as query parameters
    const url = `https://be.ratebotai.com/?data=${encodeURIComponent(data)}`;

    // Redirect the user to the constructed URL
    window.location.href = url;
  };
  return (
    <div>
        <button className='hero-btn' style={{padding:"10px 20px"}} onClick={handleButtonClick}>Book Now</button>
    </div>
  )
}

export default Button