import React, { useEffect, useState } from "react";
import { hotelFiltered } from "../../data";
import TextField from "@mui/material/TextField";
import Stack from "@mui/material/Stack";
import Autocomplete from "@mui/material/Autocomplete";


console.log(hotelFiltered);

const AutoSearch = ({func}) => {
    const [selectedOption, setSelectedOption] = useState(null);
  
    const handleOptionChange = (event, value) => {
      setSelectedOption(value);
      console.log(func(selectedOption));
      console.log(selectedOption);
      
    };
  
    return (
      <Stack spacing={2} sx={{ width: 300 }}>
        <Autocomplete
          id="free-solo-demo"
          freeSolo
          options={hotelFiltered}
          getOptionLabel={(hotelFiltered) => hotelFiltered.combain_name}
          onChange={handleOptionChange}
          renderInput={(params) => <TextField {...params} label="Search hotel or Location" />}
        />
      </Stack>
    );
  };
  
  export default AutoSearch;
